"use client";
import { motion } from "framer-motion";
import { teamData } from "@/data/team";
import TeamCard from "@/components/TeamCard";

export default function TeamSection() {
  const containerVariants = {
    hidden: {},
    visible: { transition: { staggerChildren: 0.2 } },
  };

  const titleVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.7 } },
  };

  return (
    <section className="relative py-28 px-6 bg-gradient-to-br from-sky-50 via-blue-100 to-cyan-200 overflow-hidden">
      {/* Floating gradient blobs */}
      <motion.div
        animate={{ x: [0, 30, 0], y: [0, -20, 0] }}
        transition={{ duration: 10, repeat: Infinity }}
        className="absolute top-0 left-0 w-72 h-72 bg-pink-300/30 rounded-full blur-3xl"
      ></motion.div>
      <motion.div
        animate={{ x: [0, -40, 0], y: [0, 30, 0] }}
        transition={{ duration: 12, repeat: Infinity }}
        className="absolute bottom-0 right-0 w-96 h-96 bg-indigo-300/30 rounded-full blur-3xl"
      ></motion.div>

      {/* Section title */}
      <motion.h2
        initial="hidden"
        whileInView="visible"
        variants={titleVariants}
        className="relative text-4xl md:text-5xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-sky-700 via-indigo-700 to-cyan-600 text-center mb-20 drop-shadow-lg"
      >
        {teamData.title}
      </motion.h2>

      {/* Team groups */}
      {teamData.sections.map((section, idx) => (
        <div key={idx} className="mb-24 relative z-10">
          <motion.h3
            variants={titleVariants}
            initial="hidden"
            whileInView="visible"
            className="text-3xl font-bold text-sky-900 text-center mb-10"
          >
            {section.category}
          </motion.h3>

          <motion.div
            variants={containerVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true }}
            className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-10"
          >
            {section.members.map((member) => (
              <motion.div
                key={member.name}
                initial={{ opacity: 0, scale: 0.9, y: 20 }}
                whileInView={{ opacity: 1, scale: 1, y: 0 }}
                transition={{ duration: 0.5 }}
              >
                <TeamCard {...member} />
              </motion.div>
            ))}
          </motion.div>
        </div>
      ))}
    </section>
  );
}
